/* Dashboard Responsive Styles */

.dashboard-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

.dashboard-top-grid {
  flex-shrink: 0;
}

.dashboard-bottom-grid {
  flex: 1;
  min-height: 0;
  overflow: hidden;
}

.dashboard-column {
  height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

/* Media Queries */

/* Mobile devices (up to 767px) */
@media (max-width: 767px) {
  .dashboard-column {
    height: auto;
    min-height: auto;
  }
}

/* Tablet and Desktop (768px and above) */
@media (min-width: 768px) {
  .dashboard-column {
    height: 100%;
  }
}

